.conversor {
	max-width: 300px;
	min-height: 175px;
	margin-top: 20px;
	box-shadow: 0 3px 8px 0 rgba(5, 3, 0, 0.2);
	text-align-last: center;
}
.erro {
	color: lightcoral;
}
.sucesso {
	color: lightgreen;
}

.teoria {
	justify-content: center;
	text-align: center;
}
.teoria table,
th,
td {
	border: 1px solid black;
	border-collapse: collapse;
}
